ess btn-more\" data-v-ecdf3b9a>+ 더보기</button><button class=\"btn-more-less btn-less\" data-v-ecdf3b9a>- 접기</button></div></div></div><div class=\"class-desc\" data-v-ecdf3b9a><div class=\"class-desc-col-title\" data-v-ecdf3b9a><span data-v-ecdf3b9a>어떤 클래스 인가요?</span></div><div class=\"class-desc-col-desc\" data-v-ecdf3b9a><div class=\"desc-text-wrap\" data-v-ecdf3b9a><p class=\"text_area\" data-v-ecdf3b9a><img src=\"//img.taling.me/Content/Uploads/Curri/d9a4960e0e2d16bb14c9759c67eec4c7be3e5b09.png\" alt=\"\" style=\"display:block;margin-bottom:30px;\" data-v-ecdf3b9a> 영어 일기는 내가 하고 싶은 말을 직접 쓸 수 있기 때문에 다른 어떤 영어 공부보다도 &quot;내 것&quot;으로 만들 수 있게 됩니다! 하지만 막상 시간이 확보되어도 영어로 문장을 끄적이려니 막막하실 거예요. <br data-v-ecdf3b9a><br data-v-ecdf3b9a> 그렇다면, 2주 동안 총 10번의 재밌는 밸런스 게임 주제와 내 생각을 끄적일 수 있는 고급스러운 영어 표현들을 배울 수 있다면 어떨까요?<br data-v-ecdf3b9a><br data-v-ecdf3b9a> 🐥 예를 들어볼까요? 🐥<br data-v-ecdf3b9a> &#39;집에 마스크 두고 오기&#39; VS &#39;집에 폰 두고 오기&#39; <br data-v-ecdf3b9a><br data-v-ecdf3b9a> 바로 영어로 생각하고 쓸 수 있나요? 간단해 보이지만 영어 표현으로 이게 맞나? 하면서 고민이 될 거예요.😥 <br data-v-ecdf3b9a><br data-v-ecdf3b9a> 그럼 이런 표현을 보며 쓸 수 있다면? 훨씬 수월할 거예요. <br data-v-ecdf3b9a><br data-v-ecdf3b9a> ✏ be not allowed to enter the public place 공공장소에 들어가는 게 허용되지 않는다.<br data-v-ecdf3b9a> ✏ stop somebody from doing something …이 …하는 것을 그만두게 하다.<br data-v-ecdf3b9a> ✏ End up + ing : 결국 ~하게 되다.<br data-v-ecdf3b9a> ✏ Must-have item 필수 물건<br data-v-ecdf3b9a><br data-v-ecdf3b9a> 내 생각을 표현할 수 있도록 주제별 꼭 쓰면 좋은 표현, 주제별 키워드, 그리고 영어식 표현들을 보며, 머릿속으로만 맴돌았던 내 생각을 쓸 수 있게 됩니다!😎<br data-v-ecdf3b9a><br data-v-ecdf3b9a> 처음엔 어려울 수 있어요. 하지만 튜터가 제공하는 고급 표현과 24시간 내에 제공되는 피드백을 통해 조금씩 나아지는 나를 발견할 수 있어요. 2주가 지나면 좋은 영어 표현 50문장을 체득할 수 있고, 영어 일기 쓰기가 내 일상의 루틴이 되어 있을 거예요.<br data-v-ecdf3b9a><br data-v-ecdf3b9a> 저와 밸런스 게임 해보실래요?👾 <br data-v-ecdf3b9a></p></div><div class=\"text-center\" data-v-ecdf3b9a><button class=\"btn-more-less btn-more\" data-v-ecdf3b9a>+ 더보기</button><button class=\"btn-more-less btn-less\" data-v-ecdf3b9a>- 접기</button></div></div></div></div></section>", 1)
